export default {
	data() {
		return {
			statusList: [
				"../../static/img/laosiji.png",
				"../../static/img/chezhu.png",
				"../../static/img/shuoshuo.png"

			],
			contentList: [
				"我是司机",
				"我是车主",
				"我是收货人"
			],
			statusCode: [
				"DRIVER",
				"MASTER",
				"CONSIGNEE"
			],
			current: 0,
			// 获得到首页的传参
			info: null
		}
	},

	methods: {
		getIdx(i) {
			this.current = i
		},
		// 提交数据并跳转
		async toChoosePage() {
			const opts = {
				url: "/sc/user/login",
				method: "post"
			}
			const param = {
				phone: this.info.userPhone,
				code: this.info.code,
				identityEnum: this.statusCode[this.current]
			}
			const res = await this.$http.httpRequest(opts, param);
			console.log(res)
			// 保存token到本地
			uni.setStorage({
				key: "USER_TOKEN",
				data: res.data.result,
			})
			
			// 在页面跳转之前将数据存入本地
			 uni.setStorage({
			 	key: 'USER_STATUS',
				data: this.statusCode[this.current],
			 })
			
			res.data.code === 0 && uni.reLaunch({
				url: '/pages/home/home'
			})
		}
	},

	onLoad(options) {
		//options可以接到index 传过来的值
		this.info = options
	},
}
